import React,{useEffect,useState} from 'react'
import axios from "axios";
import Banner from './Banner';
import { useNavigate } from "react-router-dom";
import Cookies from 'js-cookie';
import { useDispatch } from 'react-redux';
import {addMovie} from '../ParticularMovie'
import toast from 'react-hot-toast';

export default function Lists() {
  const navigate = useNavigate();
  const Dispatch=useDispatch();
  const [loadingMap, setLoadingMap] = useState({}); 

const [pg,setpage]=useState(1);
const [data,setdata]=useState([]);
var [fav,setFav]=useState([]);
 let [cng,setcng]=useState(1);

 //this function to authenticate
useEffect(()=>{
  
  auth();
  
},[]);

// var auth2= ()=>{
//   var c=localStorage.getItem('ashokcookie');
//   if(c==null||c.length<5)navigate('/signin');
// }

var auth=async ()=>{
  const cookies = Cookies.get('AshokCookie');
    console.log("lists",cookies);
    let c={token:cookies};
  var d=await axios.post('/isauthenticated',c,
  {headers: {'content-type': 'application/x-www-form-urlencoded'}}
   
  );
  console.log("+==",d);
  if(d.data.message!="verfied User"){
    navigate('/signin');
  }
 else{
  console.log("k",d.data);
  bringbackendfav(d.data);
  //setpage(1);
  
 }
}

var bringbackendfav=async (k)=>{
  console.log("+",typeof(k));
  var ololo=k.UserFavourite.map((b)=>{
    console.log(b.id);
    return parseInt(b.id);//Number(b.id);
   });
   setTimeout(() => {
    //console.log("=sfav=",fav);
    console.log(ololo,"ololo");
    setFav([...ololo]);
   
   }, 50);
 
    
  setTimeout(() => {
   //console.log("=sfav=",fav);
   console.log(fav,"==");
   setpage(1);
  }, 100);
}

useEffect(()=>{
  movieUpdate();
},[pg]);

async function movieUpdate(){
  var res=await axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=5540e483a20e0b20354dabc2d66a31c9&language=en-US&page=${pg}`);
  res=res.data;
  setdata(res.results);
};
var updatePage=(page)=>{
if(pg+page>0){
  
  setpage(pg+page);
};
};

var handleFavourite=async(m)=>{
  var id=parseInt(m.id);
  console.log(id);
  
  const cookies = Cookies.get('AshokCookie');
    console.log("lists",cookies);
    let c={token:cookies};


  var snd={
  token:cookies,
  movie:m
  }
  try {
    setLoadingMap((prevLoadingMap) => ({ ...prevLoadingMap, [id]: true }));
if(!fav.includes(id)){
  
  setFav([...fav,id]);
  console.log(snd);
 
   var f=await axios.post("/addfav",snd,
   {headers: {'content-type': 'application/x-www-form-urlencoded'}}
  );
  toast.success('Successfully Added to Favourites!');
}
else{
  var array=[...fav];
  const index = array.indexOf(id);
  array.splice(index, 1);
  setFav([...array]);
  var f=await axios.post("/deletefav",snd,
   {headers: {'content-type': 'application/x-www-form-urlencoded'}}
  );
  toast.success('Removed From Favourites!');
}
  }
  catch (error) {
    console.error('Error:', error);
    toast.error('An error occurred. Please try again.');
  } finally {
    setLoadingMap((prevLoadingMap) => ({ ...prevLoadingMap, [id]: false })); 
  }
 
 //console.log(fav);
}
var handleParticular=(m)=>{
  console.log(m);
   Dispatch(addMovie(m));
   navigate('/particular')
}



  return (
    <div className='router-box'>
      <Banner/>
    <div className='List'>
      <h1 className="List-top">Movies</h1>
      
      <div className='movie-list'>
        {
          data.map((movie)=>(
            <div key={movie.id} className="movie-div">
              <img className="List-img " src={`https://image.tmdb.org/t/p/original${movie.poster_path}`}alt="Card" onClick={()=>{handleParticular(movie)}}/>
              {loadingMap[movie.id] ? (
              <button className='List-button2'>Loading...</button>
            ) : fav.includes(Number(movie.id)) ? (
              <button className="List-button" onClick={() => handleFavourite(movie)}>
                Remove
              </button>
            ) : (
              <button className="List-button" onClick={() => handleFavourite(movie)}>
                Add
              </button>
            )}
            </div>
        ))
        }
      </div>
     
     {/* pagenation   btn btn-primary */}
     <nav aria-label="Page navigation example " className='List-page'>
    {/* <div className="page">
      <button className="List-page-link" aria-hidden="true" onClick={()=>updatePage(-1)}>&laquo;</button>
   
        <h6 className="List-page-link-pg">{pg}</h6>
    
        <button className="List-page-link" aria-hidden="true" onClick={()=>updatePage(1)}>&raquo;</button>
    </div> */}
    {/* <nav aria-label="Page navigation example"> */}
  <ul class="pagination">
    <li class="page-item">
      <a class="page-link" onClick={()=>updatePage(-1)}aria-label="Previous">
        <span aria-hidden="true">&laquo;</span>
      </a>
    </li>
    <li class="page-item"><a class="page-link" href="#">{pg}</a></li>
   
    <li class="page-item">
      <a class="page-link" onClick={()=>updatePage(1)}aria-label="Next">
        <span aria-hidden="true">&raquo;</span>
      </a>
    </li>
  </ul>
</nav>
{/* </nav> */}

    </div>
    </div>
  )
}

