import {createSlice} from '@reduxjs/toolkit'

const initialState={
    particular_movie:"njmkl"
  }
  
  let particularSlice=createSlice({
       name:'Particular_movie',
       initialState,
       reducers:{
         addMovie:(state,action)=>{
          state.particular_movie=action.payload
         }
  
       }
  
  })

  export const {addMovie}=particularSlice.actions
  export default particularSlice.reducer