import React ,{useState,useEffect}from 'react'
import Cookies from 'js-cookie';
import axios from "axios";
import { useNavigate } from "react-router-dom";




function Userpg() {
    const navigate = useNavigate();
    let [Username,setUsername]=useState("");
    let [UserEMAIL,setUserEMAIL]=useState("");
    
    
    useEffect(()=>{
    auth(showUser);
    
    },[]);
    
    var auth=async (showUser)=>{
        const cookies = Cookies.get('AshokCookie');
          console.log("lists",cookies);
          let c={token:cookies};
        var d=await axios.post('/isauthenticated',c,
        {headers: {'content-type': 'application/x-www-form-urlencoded'}}
         
        );
        console.log("+==",d);
        if(d.data.message!="verfied User"){
          navigate('/signin');
        }
       else{
       showUser(d.data.UserName,d.data.UserEmail);
        
       }
      }
    
    function showUser(naam,EMAIL){
        
    setUsername(naam);
    setUserEMAIL(EMAIL);
    }
    
    


  return (
   <div className='UserInfo'>
   <div className='Userblock'>
   <img className='userImg' src='https://cdn3.vectorstock.com/i/1000x1000/30/97/flat-business-man-user-profile-avatar-icon-vector-4333097.jpg'></img>
   <h1 className='userName'>UserName : {Username}</h1>
   <h1 className='userName'>UserEmail : {UserEMAIL}</h1>
   </div>
   
   </div>
  )
}

export default Userpg