import React,{useState,useEffect} from 'react'
import axios from "axios";
import { useNavigate } from 'react-router-dom';
import Cookies from 'js-cookie';
import toast from 'react-hot-toast';
export default function Logout() {
  const navigate = useNavigate();
   useEffect(()=>{
    capableToLogout();
    

   },[]);

   async function capableToLogout(){
    const cookies = Cookies.get('AshokCookie');
    console.log("lists",cookies);
    let c={token:cookies};
  var d=await axios.post('/isauthenticated',c,
  {headers: {'content-type': 'application/x-www-form-urlencoded'}}
   
  );
  if(d.data.message!="verfied User"){
    navigate('/signin')
  }


   }


  var handlelogout=async ()=>{
    console.log("logout");
try{ var d=await axios.get('/logout');
  console.log(d.data.message);
  if(d.data.message!='Succesfully logout'){
    toast.error("cant able to logout");
   }
  else{
    toast.success("Succesfully Logout");
    navigate('/');
  }
}
catch(err){
  toast.error("cant able to logout");
}
  }




  var handlehome=async ()=>{
    navigate('/');
  }

  return (
    <div className='sign'>
    <div className='form-flex'>
     
        
      
      <button onClick={handlelogout} style={{width:'90%'}}>Logout</button>
      
      <button onClick={handlehome} style={{width:'90%'}}>Home page</button>
  
    </div>
    </div>


   
  )
}
