import React ,{useState,useEffect}from 'react'
import { Link } from "react-router-dom";
import Cookies from 'js-cookie';
import axios from "axios";
export default function Navbar() {
  
 
  var [username,setusername]=useState('');
useEffect(()=>{
  changeNavbar();
  setInterval(() => {
    changeNavbar();
  }, 3000);
  
});
    

async function changeNavbar(){
  // console.log('show',show);
  const cookies= Cookies.get('AshokCookie');
  //console.log("navbar",cookies);
  let c={token:cookies};
  var d=await axios.post('/isauthenticated',c,
  {headers: {'content-type': 'application/x-www-form-urlencoded'}}
   
  );
  if(d.data.message!="verfied User"){
    setusername('');
  }
  else{
    if(username!=d.data.UserName){
      setusername(d.data.UserName);
    }
    
  }
   
 
  }

  return (
   
    <nav className="navbar navbar-expand-lg  navbar-dark bg-primary" style={{padding:"0.2rem",margin:"0rem"}}>
        <Link to="/" className="navbar-brand nav-link"style={{fontSize:"x-large",padding:"0.3rem  0.3rem 0.3rem 2rem",fontWeight:"bold"}}>ATMovies</Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto">
    <li className="nav-item ">
      <Link to="/Search" className="nav-link " style={{fontSize:"1.1rem",padding:"0.5rem"}}>Search</Link>
      </li>
      <li className="nav-item ">
      <Link to="/favourites" className="nav-link " style={{fontSize:"1.1rem",padding:"0.5rem"}}>Favourites</Link>
      </li>
      <li className="nav-item">
      <Link to="/trending" className="nav-link"style={{fontSize:"1.1rem",padding:"0.5rem"}}>  Trending</Link>
      </li>{
username!=''?
<>
<li className="nav-item">
<Link to="/logoutf" className="nav-link"style={{fontSize:"1.1rem",padding:"0.5rem"}}> logout</Link>
</li> 
<li className="nav-item">
<Link to="/User" className="nav-link"style={{fontSize:"1.1rem",padding:"0.5rem",color:'white'}}>{username}</Link>
</li> 
</>  :
<>
<li className="nav-item">
<Link to="/signin" className="nav-link"style={{fontSize:"1.1rem",padding:"0.5rem"}}> Sign In</Link>
</li>
<li className="nav-item">
<Link to="/signup" className="nav-link"style={{fontSize:"1.1rem",padding:"0.5rem"}}> Sign Up</Link>
</li>

</>


      }
          
    </ul>
  </div>
</nav>

   
  )
}
